AEmC;EACnC,6BAA6B;AAC/B;;AAEA;;EAEE,WAAW;EACX,YAAY;EACZ,cAAc;EACd,iBAAiB;EACjB,mBAAmB;EACnB,6BAA6B;AAC/B;;AAEA;EACE,iBAAiB;AACnB;;AAEA,iBAAiB;;AAEjB;EACE,UAAU;EACV,wBAAwB;AAC1B;;AAEA;;EAEE,iBAAiB;EACjB,sBAAsB;AACxB;;AAEA,kBAAkB;;AAElB;EACE,2BAA2B;AAC7B;;AAEA,iCAAiC;;AAEjC;EACE,aAAa;EACb,sBAAsB;EACtB,mBAAmB;EACnB,SAAS;EACT,mBAAmB;AACrB;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,SAAS;EACT,aAAa;EACb,mBAAmB;EACnB,gBAAgB;EAChB;;mCAEiC;EACjC,eAAe;EACf;;mBAEiB;AACnB;;AAEA;EACE;;;;GAIC;AACH;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,mBAAmB;EACnB,iBAAiB;EACjB,cAAc;AAChB;;AAEA;EACE,kBAAkB;EAClB,YAAY;EACZ,6BAA6B;EAC7B,UAAU;EACV,eAAe;EACf,cAAc;EACd,wCAAwC;AAC1C;;AAEA;EACE,eAAe;EACf,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,2CAA2C;EAC3C,WAAW;AACb;;AAEA;EACE,OAAO;EACP,aAAa;EACb,uBAAuB;EACvB,mBAAmB;EACnB,QAAQ;AACV;;AAEA;EACE,WAAW;EACX,2BAA2B;AAC7B;;AAEA;EACE,iBAAiB;AACnB;;AAEA;;;EAGE,iBAAiB;EACjB,wBAAwB;AAC1B","sourcesContent":["/* 1. Use a more-intuitive box-sizing model */\n*,\n*::before,\n*::after {\n  box-sizing: border-box;\n}\n\n/* 2. Remove default margin */\n*:not(dialog) {\n  margin: 0;\n}\n\n/* 3. Enable keyword animations */\n@media (prefers-reduced-motion: no-preference) {\n  html {\n    interpolate-size: allow-keywords;\n  }\n}\n\nbody {\n  /* 4. Add accessible line-height */\n  line-height: 1.5;\n  /* 5. Improve text rendering */\n  -webkit-font-smoothing: antialiased;\n}\n\n/* 6. Improve media defaults */\nimg,\npicture,\nvideo,\ncanvas,\nsvg {\n  display: block;\n  max-width: 100%;\n}\n\n/* 7. Inherit fonts for form controls */\ninput,\nbutton,\ntextarea,\nselect {\n  font: inherit;\n}\n\n/* 8. Avoid text overflows */\np,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  overflow-wrap: break-word;\n}\n\n/* 9. Improve line wrapping */\np {\n  text-wrap: pretty;\n}\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  text-wrap: balance;\n}\n\n/*\n  10. Create a root stacking context\n*/\n#root,\n#__next {\n  isolation: isolate;\n}\n\n:root {\n  --text: hsl(0, 0%, 95%);\n  --text-muted: hsl(0, 0%, 80%);\n  --section-spacing: 5rem;\n}\n\nhtml {\n  height: 100%;\n}\n\nhtml,\nbody {\n  min-height: 100%;\n}\n\nbody {\n  background: linear-gradient(120deg, #1a1a1a, #222);\n  color: #f5f5f5;\n}\n\n.section-dark {\n  background: #1f1f1f;\n}\n\n.section-elevated {\n  background: #262626;\n}\n\n.section-animated {\n  background: linear-gradient(120deg, #1f1f1f, #2a2a2a, #1f1f1f);\n  background-size: 200% 200%;\n  animation: moveGradient 8s ease infinite;\n}\n\n@media (max-width: 600px) {\n  .section-animated {\n    background: linear-gradient(100deg, #232323, #292929, #232323);\n    background-size: 120% 120%;\n    animation-duration: 12s;\n  }\n}\n\n@keyframes moveGradient {\n  0% {\n    background-position: 0% 50%;\n  }\n  50% {\n    background-position: 100% 50%;\n  }\n  100% {\n    background-position: 0% 50%;\n  }\n}\n\n.section-title {\n  font-size: clamp(1.5rem, 7.5vw, 2.5rem);\n  letter-spacing: 0.2em;\n  text-transform: uppercase;\n}\n\n.material-symbols-outlined {\n  font-size: 22px;\n  line-height: 1.5;\n  font-variation-settings:\n    'FILL' 0,\n    'wght' 400,\n    'GRAD' 0,\n    'opsz' 20;\n}\n\nimg {\n  filter: brightness(0.8) grayscale(15%);\n}\n\n/* Hero Section */\n\n.hero {\n  height: 100vh;\n  display: flex;\n  flex-direction: column;\n  position: relative;\n  background:\n    linear-gradient(rgba(0, 0, 0, 0.01), rgba(0, 0, 0, 0.01)),\n    url(images-hero/young-dad-centered.png);\n  background-size: cover;\n  background-position: center;\n}\n\n.hero::after {\n  content: '';\n  position: absolute;\n  bottom: 0;\n  left: 0;\n  width: 100%;\n  height: 160px;\n\n  background: linear-gradient(\n    to bottom,\n    oklch(42.761% 0.00005 271.152 / 0),\n    #212222\n  );\n}\n\nnav {\n  position: sticky;\n  top: 4px;\n  z-index: 2;\n  display: flex;\n  justify-content: flex-end;\n  gap: 16px;\n  padding-top: 4px;\n  padding-right: 16px;\n}\n\nnav .material-symbols-outlined {\n  font-size: 32px;\n  color: var(--text-muted);\n}\n\n.hero-message {\n  /* position: absolute;\n  top: 18vh; */\n  padding-top: 8vh;\n  text-align: center;\n  display: flex;\n  flex-direction: column;\n  gap: 20px;\n}\n\nh1,\nh2 {\n  font-size: clamp(1.8rem, 9vw, 2.5rem);\n  white-space: nowrap;\n  font-family: 'Playfair Display';\n  color: var(--text);\n}\n\np {\n  padding: 0 8px;\n  font-size: clamp(1rem, 5vw, 1.25rem);\n  font-family: 'Inter';\n  color: var(--text-muted);\n}\n\n.down-arrow {\n  position: absolute;\n  bottom: 20px;\n  left: 50%;\n  transform: translate(-50%, 10%);\n  font-size: 68px;\n  color: var(--text-muted);\n  z-index: 2;\n\n  opacity: 0;\n  animation:\n    arrowIntro 0.5s 4s ease-out forwards,\n    arrowBounce 0.9s ease-in-out 4s 3;\n}\n\n/* Fade + slight drop on load */\n@keyframes arrowIntro {\n  0% {\n    opacity: 0;\n    transform: translate(-50%, -10%);\n  }\n  100% {\n    opacity: 1;\n    transform: translate(-50%, 10%);\n  }\n}\n\n/* Bounce to indicate scroll */\n@keyframes arrowBounce {\n  0%,\n  100% {\n    transform: translate(-50%, 10%);\n  }\n  50% {\n    transform: translate(-50%, 35%);\n  }\n}\n\n/* ===== 3D Slider Section ===== */\n\n.slider-section {\n  position: relative;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  gap: 20px;\n  padding: 100px 0;\n  /* background: linear-gradient(\n    to bottom,\n    #2a2a2a 0%,\n    #282828 100%\n  ); */\n  /* background-color: #1f1f1f; */\n}\n\n.section-title::after {\n  content: '';\n  display: block;\n  width: 80px;\n  height: 2px;\n  margin: 12px auto 0;\n  background: linear-gradient(to right, transparent, #ffb478, transparent);\n}\n\n@keyframes moveGradient {\n  0% {\n    background-position: 0% 50%;\n  }\n  50% {\n    background-position: 100% 50%;\n  }\n  100% {\n    background-position: 0% 50%;\n  }\n}\n\n.slider-section::before {\n  content: '';\n  position: absolute;\n  top: -80px;\n  left: 0;\n  width: 100%;\n  height: 80px;\n  background: linear-gradient(to bottom, transparent, #2a2a2a);\n}\n\n/* Slider Container */\n.slider {\n  position: relative;\n  width: 100%;\n  height: 70vh;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  overflow: hidden;\n  perspective: 1200px;\n  background: transparent;\n}\n\n/* Track */\n.slider__track {\n  position: relative;\n  width: 100%;\n  height: 100%;\n  transform-style: preserve-3d;\n  margin-bottom: 40px;\n}\n\n/* Slides */\n.slider__slide {\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  width: 100%;\n  max-width: 83vw;\n  height: 80%;\n\n  will-change: transform;\n  transform-style: preserve-3d;\n  transition:\n    transform 0.9s ease,\n    opacity 0.6s ease;\n  transform-origin: center;\n\n  -webkit-box-reflect: below 5px\n    linear-gradient(transparent, transparent, rgba(3, 3, 3, 0.1));\n}\n\n.slider__slide img {\n  width: 100%;\n  height: 100%;\n  object-fit: cover;\n  border-radius: 20px;\n  /* box-shadow: 0 25px 60px rgba(0, 0, 0, 0.4); */\n  /* box-shadow: rgba(0, 0, 0, 0.45) 0px 25px 20px -20px; */\n  user-select: none;\n  pointer-events: none;\n}\n\n/* ===== Before-After Section ===== */\n\n.before-after-section {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  gap: 100px;\n  padding: 100px 20px;\n}\n\n.comparison-slider {\n  position: relative;\n  max-width: 340px;\n  border-radius: 20px;\n  overflow: hidden;\n  aspect-ratio: 4 / 5;\n  box-shadow:\n    rgba(0, 0, 0, 0.25) 0px 14px 28px,\n    rgba(0, 0, 0, 0.22) 0px 10px 10px;\n}\n\n.slider-image {\n  width: 100%;\n  height: 100%;\n  object-fit: cover;\n  object-position: left;\n}\n\n.image-before {\n  position: absolute;\n  inset: 0;\n  width: 100%;\n  height: 100%;\n  object-fit: cover;\n  z-index: 1;\n  pointer-events: none;\n  clip-path: inset(0 calc(100% - var(--position)) 0 0);\n}\n\n.slider-range {\n  position: absolute;\n  inset: 0;\n  cursor: pointer;\n  opacity: 0;\n  /* for Firefox */\n  width: 100%;\n  height: 100%;\n}\n\n.slider-range:focus-visible ~ .slider-button {\n  outline: 5px solid black;\n  outline-offset: 3px;\n}\n\n.slider-line {\n  position: absolute;\n  inset: 0;\n  width: 0.2rem;\n  height: 100%;\n  background-color: #fff;\n  z-index: 10;\n  left: var(--position);\n  transform: translateX(-50%);\n  pointer-events: none;\n}\n\n.slider-button {\n  position: absolute;\n  background-color: #fff;\n  color: black;\n  padding: 0.5rem;\n  border-radius: 100vw;\n  display: grid;\n  place-items: center;\n  top: 50%;\n  left: var(--position);\n  transform: translate(-50%, -50%);\n  pointer-events: none;\n  z-index: 100;\n  box-shadow: 1px 1px 1px hsl(0, 50%, 2%, 0.5);\n  overflow: hidden;\n}\n\n/* ===== Timeline Section ===== */\n\n.timeline-section {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  padding: 100px 20px;\n  position: relative;\n  overflow-x: hidden;\n}\n\n.timeline {\n  position: relative;\n  max-width: 1100px;\n  margin: 0 auto;\n}\n\n/* Vertical Line */\n\n.timeline-line {\n  position: absolute;\n  left: 50%;\n  top: 0;\n  width: 2px;\n  height: 100%;\n  background: rgba(255, 255, 255, 0.08);\n  transform: translateX(-50%);\n}\n\n/* Timeline Items */\n\n.timeline-item {\n  position: relative;\n  margin: 100px 0;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  min-height: 70vh;\n}\n\n.timeline-content {\n  width: 100%;\n  max-width: 500px;\n  text-align: center;\n  transition: all 0.8s ease-out;\n  opacity: 0;\n  transform: translateY(20px);\n}\n\n/* Year */\n\n.timeline-year {\n  display: block;\n  margin-bottom: 15px;\n  font-size: 1.12rem;\n  letter-spacing: 2px;\n  color: var(--text-muted);\n}\n\n/* Card */\n\n.timeline-card {\n  border-radius: 20px;\n  aspect-ratio: 4 / 5;\n  overflow: hidden;\n  background: rgba(255, 255, 255, 0.03);\n  /* box-shadow:\n    0 20px 40px rgba(0, 0, 0, 0.6),\n    0 0 40px rgba(255, 255, 255, 0.03); */\n  box-shadow:\n    rgba(0, 0, 0, 0.25) 0px 14px 28px,\n    rgba(0, 0, 0, 0.22) 0px 10px 10px;\n  transition: all 0.8s ease-out;\n}\n\n.timeline-card img,\n.timeline-card video {\n  width: 100%;\n  height: 100%;\n  display: block;\n  filter: blur(6px);\n  transform: scale(1);\n  transition: all 0.8s ease-out;\n}\n\n.timeline-card video {\n  object-fit: cover;\n}\n\n/* Active State */\n\n.timeline-item.active .timeline-content {\n  opacity: 1;\n  transform: translateY(0);\n}\n\n.timeline-item.active .timeline-card img,\n.timeline-item.active .timeline-card video {\n  filter: blur(0px);\n  transform: scale(1.04);\n}\n\n/* Floating Feel */\n\n.timeline-card:hover {\n  transform: translateY(-6px);\n}\n\n/* ===== Messages Section ===== */\n\n.family-messages-section {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  gap: 40px;\n  padding: 100px 20px;\n}\n\n.family-messages-section .section-title {\n  margin-bottom: 40px;\n}\n\n.audio-card {\n  display: flex;\n  align-items: center;\n  gap: 1rem;\n  padding: 1rem;\n  border-radius: 16px;\n  background: #111;\n  box-shadow:\n    rgba(0, 0, 0, 0.19) 0px 10px 20px,\n    rgba(0, 0, 0, 0.23) 0px 6px 6px;\n  min-width: 90vw;\n  transition:\n    background 0.7s,\n    box-shadow 0.7s;\n}\n\n.audio-card.is-playing {\n  background: linear-gradient(\n    to right,\n    rgba(200, 155, 94, 0.06),\n    rgba(0, 0, 0, 0.9)\n  );\n}\n\n.audio-avatar {\n  width: 84px;\n  height: 84px;\n  border-radius: 100%;\n  object-fit: cover;\n  flex-shrink: 0;\n}\n\n.audio-play-btn {\n  border-radius: 50%;\n  border: none;\n  background-color: transparent;\n  padding: 0;\n  cursor: pointer;\n  flex-shrink: 0;\n  -webkit-tap-highlight-color: transparent;\n}\n\n.play-icon {\n  font-size: 36px;\n  color: var(--text);\n}\n\n.audio-wave-wrapper {\n  display: grid;\n  grid-template-rows: max-content max-content;\n  width: 100%;\n}\n\n.audio-button-wrapper {\n  flex: 1;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  gap: 8px;\n}\n\n.audio-wave {\n  width: 100%;\n  transition: box-shadow 0.3s;\n}\n\n.audio-time {\n  justify-self: end;\n}\n\n.audio-current-time,\n.audio-duration,\n.slash {\n  font-size: 0.7rem;\n  color: var(--text-muted);\n}\n"],"sourceRoot":""}]);
